import React from 'react';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import IconButton from '@material-ui/core/IconButton';

import './navigation.css';

import socialButtons from './socialButtons.js';


class NavigationBar extends React.PureComponent {
	renderTabs() {
		return (
			this.props.tabs.map(tab =>
				<Tab
				key={tab.id}
				label={tab.label}
				/>
			)
		);
	}

	handleTabChange(event, index) {
		const tab = this.props.tabs[index];
		window.history.pushState(tab.id, '', './' + tab.id);
		this.props.onTabChange(index);
	}

	renderSocialButtons() {
		const buttons = [];
		for (const [key, data] of Object.entries(socialButtons)) {
			buttons.push(
				<a key={key} href={data.url}>
				<IconButton className='social-button'>
				<img
				className='social-icon'
				src={data.icon}
				alt={key + ' social media icon'}
				/>
				</IconButton>
				</a>
			);
		}
		return buttons;
	}

	render() {
		const logo = (
			<div id='main-logo-container'>
			<a href='/' id='main-logo'>
			Samuel J. Li
			</a>
			</div>
		);
		const socialButtons = (
			<div id='social-buttons'>
			{this.renderSocialButtons()}
			</div>
		);

		return (
			<AppBar id='navigation-bar'>
				<Toolbar>
					{logo}
					<div className='desktop navigation-tabs'>
					<Tabs
						value={this.props.tabIndex}
						onChange={(event, value) => this.handleTabChange(event, value)}
					>
					{this.renderTabs()}
					</Tabs>
					</div>
					{socialButtons}
				</Toolbar>
				<div className='mobile navigation-tabs'>
					<Tabs
						value={this.props.tabIndex}
						onChange={(event, value) => this.handleTabChange(event, value)}
						fullWidth
					>
					{this.renderTabs()}
					</Tabs>
				</div>
			</AppBar>
		);
	}
}

export default NavigationBar;
